  margin: 0 0 12px 0;
  border: 1px solid #E8BE5D;
  border-radius: 5px;
  padding: 4px 1em;
  background-color: #E8BE5D;
  color: #FAFAFA;
  white-space: normal;
  font-size: 20px;
  line-height: 24px;
}

input[type=button]:hover,
input[type=submit]:hover,
input[type=reset]:hover,
button:hover {
  background-color: #D4AA49 !important;
  color: #FAFAFA;
}

input[type=button]:active,
input[type=submit]:active,
input[type=reset]:active {
