html {
  height: 95vh;
  width: auto;
}

body {
  background: #FFFFFF;
  color: #212C4C;
  padding: 0;
  margin: 0 2em;
  display: flex;
  flex-direction: column;
  font-size: 20px;
  line-height: 24px;
  font-weight: 400;
}

html[dir=rtl] body,
html[dir=rtl] input,
html[dir=rtl] textarea,
html[dir=rtl] select {
  font-family: "Quicksand", "QuicksandLocal", serif;
}

body,
input,
textarea,
select {
  font-family: "Quicksand", "QuicksandLocal", sans-serif;
}

body > form {
  min-height: 95vh;
  max-height: -webkit-fill-available;
  display: grid;
  grid-template-columns: 1fr minmax(343px, 1660px) 1fr;
  grid-template-rows: 130px auto 110px;
  grid-template-areas: ". header ." ". content ." ". footer .";
}

.header {
  grid-area: header;
  box-sizing: border-box;
  height: 130px;
  padding-bottom: 1em;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  border-bottom: 1px solid #8797C8;
}

.surroundcontent {
  grid-area: content;
  min-height: 66vh;
}

.footer {
  grid-area: footer;
  height: 110px;
  border-top: 1px solid #8797C8;
  padding: 1em 0;
}

.hiddencontrol {
  position: absolute;
  left: -9999px;
  width: 1px;
  height: 1px;
}

html[dir=rtl] .hiddencontrol {
  left: 9999px;
}

.unavailable {
  visibility: hidden;
}

.nowrap {
  white-space: nowrap;
}

.align-left {
  text-align: left !important;
}

.align-center {
  text-align: center !important;
}

.align-right {
  text-align: right !important;
}

@media screen and (max-width: 1023px) {
  body {
    margin: 0;
  }
  .header {
    height: 75px;
    padding: 0 1em 0.8em 1em;
  }
  .footer {
    padding: 1em;
  }
  body > form {
    grid-template-rows: 75px auto 110px;
  }
}
input:disabled {
  opacity: 1;
}

*:focus,
*:focus-visible,
*:focus-within,
*:focus + label,
*:focus-visible + label,
*:focus-within + label {
  border-color: #E8BE5D !important;
}

/* Standardises the placeholder colour - these rules cannot be combined */
::-moz-placeholder {
  color: #4A444E;
  opacity: 1;
  font-size: 12px;
}

:-ms-input-placeholder {
  color: #4A444E;
  opacity: 1;
  font-size: 12px;
}

::placeholder {
  color: #4A444E;
  opacity: 1;
  font-size: 12px;
}

:-ms-input-placeholder {
  color: #4A444E;
  font-size: 12px;
}

::-ms-input-placeholder {
  color: #4A444E;
  font-size: 12px;
}

sup {
  vertical-align: top;
  font-size: 0.6em;
  position: relative;
  top: -4px;
}

sub {
  vertical-align: bottom;
  font-size: 0.6em;
  position: relative;
  top: 4px;
}